.feedback {
  margin-bottom: 87px;
}

.feedback__headline {
  margin: 0 0 26px 0;

  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
}

.feedback__text {
  margin: 0 0 30px 0;

  font-size: 15px;
  line-height: 30px;
  color: $charcoal-color;
}

.feedback__name {
  margin: 0;

  line-height: 24px;
  font-weight: 700;
}

.feedback__nickname {
  margin: 0;

  line-height: 30px;
  color: $charcoal-color;
}

.feedback__author-wrapper {
  margin: 0 0 35px 0;
}

.feedback__button-wrapper {
  display: flex;
  margin: 0 0 38px 0;

  text-align: center;
}

.feedback__button {
  padding: 11px 42px 10px 36px;

  background-color: #ffffff;
  border: 1px solid #ececec;
}

.feedback__button:hover .feedback__image,
.feedback__button:focus .feedback__image {
  opacity: 0.6;
}

.feedback__button:active .feedback__image {
  opacity: 0.3;
}

@media (min-width: $tablet-width) {
  .feedback {
    display: flex;
    flex-direction: column;
    position: relative;
    margin-bottom: 148px;

    &::before {
      content: "";

      position: absolute;
      top: 0;
      left: -50px;
      z-index: -1;
      width: 350px;
      height: 456px;

      background-image: url("../img/review@1x.jpg");
      background-repeat: none;

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/review@2x.jpg");
        background-size: 350px auto;
      }
    }
  }

  .feedback__headline {
    margin: 78px 0 48px 0;
    width: 300px;

    font-size: 43px;
    line-height: 50px;
  }

  .feedback__text {
    width: 620px;
    margin-bottom: 59px;

    font-size: 17px;
  }

  .feedback__author-wrapper {
    width: 245px;
    margin-bottom: 45px;
  }

  .feedback__name {
    margin-bottom: 2px;

    font-size: 18px;
  }

  .feedback__button-wrapper {
    position: absolute;
    bottom: 47px;
    right: 0;
    margin: 0;
  }

  .feedback__button {
    padding: 10px 19px;

    border: none;
  }

  .feedback__write {
    position: absolute;
    top: 79px;
    right: 0;
    padding: 14px 38px;

    font-size: 17px;
  }
}

@media (min-width: $desktop-width) {
  .feedback {
    margin-bottom: 233px;
    padding: 0 50px;

    background-image: url("../img/review@1x.jpg");
    background-position: 50px 0;
    background-repeat: no-repeat;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/review@2x.jpg");
      background-size: 350px auto;
    }

    &::before {
      content: none;
    }
  }

  .feedback__headline {
    margin: 72px 0 48px 174px;
    width: 230px;

    font-size: 53px;
    line-height: 57px;
  }

  .feedback__text {
    width: 740px;
    margin: 0 0 39px  174px;
  }

  .feedback__author-wrapper {
    margin: 0 0 95px  174px;
  }

  .feedback__name {
    font-size: 17px;
  }

  .feedback__button-wrapper {
    bottom: 97px;
    right: 50px;
  }

  .feedback__write {
    top: 79px;
    right: 50px;
  }
}
